<template>
    <div>
        <div class="container">
            <div class="handle-box">
                <el-button
                        type="primary"
                        icon="el-icon-delete"
                        class="handle-del mr10"
                        @click="handleAdd"
                >添加</el-button>

                <el-select v-model.number="query.buyLevel" placeholder="请选择">
                    <el-option v-for="item in BuyLevelTypeAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="query.filmName" placeholder="电影名称" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
                    :summary-method="getSummaries"
                    show-summary
            >
<!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="FilmName" label="电影名称"></el-table-column>
                <el-table-column label="会员级别">
                    <template slot-scope="scope">{{scope.row.BuyLevel|MemberLevelName}}</template>
                </el-table-column>
                <el-table-column label="项目总额">
                    <template slot-scope="scope">{{scope.row.ProjectGold|money1}}</template>
                </el-table-column>
                <el-table-column label="已购金额">
                    <template slot-scope="scope">{{scope.row.CurProjectGold|money1}}</template>
                </el-table-column>
                <el-table-column label="虚购金额">
                    <template slot-scope="scope">{{scope.row.CurVirtualProjectGold|money1}}</template>
                </el-table-column>
                <el-table-column label="起投金额">
                    <template slot-scope="scope">{{scope.row.BuyMinGoldLimit|money1}}</template>
                </el-table-column>
                <el-table-column label="递增金额">
                    <template slot-scope="scope">{{scope.row.StepGold|money1}}</template>
                </el-table-column>
                <el-table-column label="购买次数限制">
                    <template slot-scope="scope">{{scope.row.BuyCountLimit}}</template>
                </el-table-column>
                <el-table-column label="结算方式">
                    <template slot-scope="scope">{{scope.row.SettlementModesName}}</template>
                </el-table-column>
                <el-table-column label="周期" width="60">
                    <template slot-scope="scope">{{scope.row.RebateCycle}}</template>
                </el-table-column>
                <el-table-column label="日化利率" width="80">
                    <template slot-scope="scope">{{scope.row.DailyRate*100|money}}%</template>
                </el-table-column>
                <el-table-column label="进度" width="80">
                    <template slot-scope="scope">{{(scope.row.CurProjectGold+scope.row.CurVirtualProjectGold)/scope.row.ProjectGold*100|money1}}%</template>
                </el-table-column>
                <el-table-column label="状态" width="80">
                    <template slot-scope="scope">{{scope.row.State|FilmStateName}}</template>
                </el-table-column>

                <el-table-column label="操作" width="140" align="center">
                    <template slot-scope="scope">
<!--                        <el-button-->
<!--                                type="text"-->
<!--                                icon="el-icon-edit"-->
<!--                                @click="handleEdit(scope.$index, scope.row)"-->
<!--                        >查看</el-button>-->
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                class="red"
                                @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>


        <!-- 编辑弹出框 -->
        <el-dialog :before-close="handleClose" title="编辑" :visible.sync="editVisible" width="90%">

