                <el-form-item label="电影名称" prop="FilmName">
                    <el-input v-model="form.FilmName"></el-input>
                </el-form-item>
                <el-form-item label="英文名" prop="FilmNameE">
                    <el-input v-model="form.FilmNameE"></el-input>
                </el-form-item>
                <el-form-item label="其它译名" prop="FilmNameO">
                    <el-input v-model="form.FilmNameO"></el-input>
                </el-form-item>

                <el-form-item label="出品公司" prop="BulletFilms">
