                        <img v-if="form.UpdatedUrl" :src="form.UpdatedUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delUpdatedUrl()"  >删除</button>

                </el-form-item>
                <el-form-item label="剧照(240*150)" prop="StillsUrl">
<!--                    <el-input v-model="form.StillsUrl" readonly></el-input>-->
                    <el-upload
