                        <img v-if="form.StillsUrl" :src="form.StillsUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.StillsUrl" @click="delStillsUrl()"  >删除</button>
                </el-form-item>
                <el-divider></el-divider>

                <el-form-item label="轮播图1(240*150)" prop="UpdatedUrl1">
                    <el-upload
                            class="avatar-actor-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-change="handleChangeUpdatedUrl1"
