                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delUpdatedUrl1()"  >删除</button>
                </el-form-item>

                <el-form-item label="轮播图2(240*150)" prop="UpdatedUrl2">
                    <el-upload
