                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delUpdatedUrl2()"  >删除</button>
                </el-form-item>
                <el-form-item label="轮播图3(240*150)" prop="UpdatedUrl3">
                    <el-upload
                            class="avatar-actor-uploader"
