                            :auto-upload="false" >
                        <img v-if="form.ActorUrl2" :src="form.ActorUrl2" class="avatar-actor">
                        <i v-else class="el-icon-plus avatar-actor-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delActorUrl2()"  >删除</button>
                </el-form-item>

                <el-form-item label="演员3" prop="ActorDes3">
                    <el-input v-model="form.ActorDes3"></el-input>
                    <el-upload
                            class="avatar-actor-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-change="handleChangeActorUrl3"
                            :show-file-list="false"
                            :auto-upload="false" >
                        <img v-if="form.ActorUrl3" :src="form.ActorUrl3" class="avatar-actor">
                        <i v-else class="el-icon-plus avatar-actor-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delActorUrl3()"  >删除</button>
                </el-form-item>
                <el-form-item label="演员4" prop="ActorDes4">
                    <el-input v-model="form.ActorDes4"></el-input>
                    <el-upload
                            class="avatar-actor-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-change="handleChangeActorUrl4"
                            :show-file-list="false"
                            :auto-upload="false" >
                        <img v-if="form.ActorUrl4" :src="form.ActorUrl4" class="avatar-actor">
                        <i v-else class="el-icon-plus avatar-actor-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delActorUrl4()"  >删除</button>
                </el-form-item>
                <el-form-item label="演员5" prop="ActorDes5">
                    <el-input v-model="form.ActorDes5"></el-input> 
                    <el-upload
                            class="avatar-actor-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-change="handleChangeActorUrl5"
                            :show-file-list="false"
                            :auto-upload="false" >
                        <img v-if="form.ActorUrl5" :src="form.ActorUrl5" class="avatar-actor">
                        <i v-else class="el-icon-plus avatar-actor-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delActorUrl5()"  >删除</button>
                </el-form-item>

                <el-form-item label="演员6" prop="ActorDes6">
                    <el-input v-model="form.ActorDes6"></el-input>
                    <el-upload
                            class="avatar-actor-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :on-change="handleChangeActorUrl6"
                            :show-file-list="false"
                            :auto-upload="false" >
                        <img v-if="form.ActorUrl6" :src="form.ActorUrl6" class="avatar-actor">
                        <i v-else class="el-icon-plus avatar-actor-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.UpdatedUrl" @click="delActorUrl6()"  >删除</button>
                </el-form-item>

                <el-divider></el-divider>
                <el-form-item label="会员级别"  prop="BuyLevel">
                    <el-select v-model.number="form.BuyLevel" placeholder="请选择">
                        <el-option v-for="item in BuyLevelTypes" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="状态"  prop="State">
                    <el-select v-model.number="form.State" placeholder="请选择">
                        <el-option v-for="item in FilmStates" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="电影代码">
                    <el-input v-model="form.ProjectCode"></el-input>
                </el-form-item>
                <el-form-item label="担保机构">
                    <el-input v-model="form.Guarantors"></el-input>
                </el-form-item>
                <el-form-item label="资金用途">
                    <el-input v-model="form.Funds"></el-input>
                </el-form-item>

                <el-form-item label="项目总额">
                    <el-input-number class="loose-input"  v-model="form.ProjectGold" :precision="2" :step="100" :min="0"></el-input-number>
                </el-form-item>

                <el-form-item label="已投人数">
                    <el-input-number class="loose-input"  v-model="form.CurVirtualPLCount" :precision="0" :step="1" :min="0"></el-input-number>
                </el-form-item>
                <el-form-item label="已投金额">
                    <el-input-number class="loose-input"  v-model="form.CurVirtualProjectGold" :precision="2" :step="100"  :min="0"></el-input-number>
                </el-form-item>
                <el-form-item label="起投金额">
                    <el-input-number class="loose-input"  v-model="form.BuyMinGoldLimit" :precision="2" :step="100" :min="1"></el-input-number>
                </el-form-item>

                <el-form-item label="递增金额">
                    <el-input-number class="loose-input"  v-model="form.StepGold"   :step="100" :min="0"></el-input-number>
                </el-form-item>

                <el-form-item label="购买次数限制">
                    <el-input-number class="loose-input"  v-model="form.BuyCountLimit"   :step="1" :min="1"></el-input-number>
                </el-form-item>

                <el-form-item label="返利周期">
                    <el-input-number class="loose-input"  v-model="form.RebateCycle" :precision="0" :step="1" :min="1"></el-input-number>
                </el-form-item>

                <el-form-item label="日化收益">
                    <el-input-number class="loose-input"  v-model="form.DailyRate" :precision="3" :step="0.01" :min="0" ></el-input-number>
                </el-form-item>

<!--                <el-form-item label="项目期限">-->
<!--                    <el-input-number class="loose-input"  v-model="form.ProjectDeadline" :precision="0" :step="2"  :min="0"></el-input-number>-->
<!--                </el-form-item>-->

                <el-form-item label="投资模式">
                <el-select v-model.number="form.SettlementWay" placeholder="请选择">
                    <el-option v-for="item in SettlementModesNameAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                </el-form-item>

                <el-form-item label="复投模式">
                    <el-select v-model.number="form.InterestWay" placeholder="请选择">
                        <el-option v-for="item in InterestWayNameAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
