</template>
<script>
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';

    export default {
        name: 'FilmList',
        data() {
            return {
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],

                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                SettlementModesNameAll:[{name:'1.每日付收益、到期返本',value:1},
                    {name:'2.一次性返本付息',value:2},
                    {name:'3.时时返模式',value:3}
                    ],
                InterestWayNameAll:[{name:'否',value:1},
                    {name:'是',value:2}
                ],
                FilmStateAll:[
                    {name:'全部',value:0},
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                FilmStates:[
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                BuyLevelTypeAll:[
                    {name:'全部',value:0},
                    {name:'新手会员',value:1},
                    {name:'白银会员',value:2},
                    {name:'黄金会员',value:3},
                    {name:'钻石会员',value:4},
                    {name:'皇冠会员',value:5}
                ],
                BuyLevelTypes:[
                    {name:'新手会员',value:1},
                    {name:'白银会员',value:2},
                    {name:'黄金会员',value:3},
                    {name:'钻石会员',value:4},
                    {name:'皇冠会员',value:5}
                ],
                query: {
                    buyLevel: 0,
                    filmName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                rules: {
                    FilmName: [
                        { required: true, message: '请输入电影名称', trigger: 'blur' },
                        { min: 2, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur' }
                    ],
                    BuyLevel: [
                        { required: true, message: '请选择会员级别', trigger: 'blur' },
                        { min: 1, max: 5, message: '会员级别只能在1~5之间', trigger: 'blur' }
                    ],
                    State: [
                        { required: true, message: '请选择状态', trigger: 'blur' },
                        { min: 1, max: 3, message: '状态只能在1~3之间', trigger: 'blur' }
                    ],
                    ProjectGold: [
                        { required: true, message: '请填写项目总额', trigger: 'blur' },
                        { min: 1, message: '项目总额最少为1', trigger: 'blur' }
                    ],
                    CurVirtualPLCount: [
                        { required: true, message: '请填写已投人数', trigger: 'blur' },
                        { min: 0, message: '已投人数最少为0', trigger: 'blur' }
                    ],
                    CurVirtualProjectGold: [
                        { required: true, message: '请填写已投金额', trigger: 'blur' },
                        { min: 0, message: '已投金额最少为0', trigger: 'blur' }
                    ],
                    RebateCycle: [
                        { required: true, message: '请填写返利周期', trigger: 'blur' },
                        { min: 1, message: '返利周期最少为1', trigger: 'blur' }
                    ],
                    DailyRate: [
                        { required: true, message: '请填写日化收益', trigger: 'blur' },
                        { min: 0, message: '日化收益最少为0', trigger: 'blur' }
                    ],
                    ProjectDeadline: [
                        { required: true, message: '请填写项目期限', trigger: 'blur' },
                        { min: 1, message: '项目期限最少为0', trigger: 'blur' }
                    ],
                },

                tableData: [],
                GroupData:{},
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1,
                formData: new FormData(),

                ActorUrl1File:{},
                ActorUrl2File:{},
                ActorUrl3File:{},
                ActorUrl4File:{},
                ActorUrl5File:{},
                ActorUrl6File:{},

                UpdatedUrlFile:{},
                FilmUpdatedUrl1File:{},
                FilmUpdatedUrl2File:{},
                FilmUpdatedUrl3File:{},
                FilmVideoUrlFile:{},

            };
        },
        created() {
            this.getData();
        },
        components: {
            VueCropper
        },
        methods: {
            getSummaries(){
                const sums = [];
                sums[1] = "合计："+ this.GroupData.C+"条";
                sums[3] = this.GroupData.ProjectGold;
                sums[4] = this.GroupData.CurProjectGold;
                sums[5] = this.GroupData.CurVirtualProjectGold;
                return sums;
            },
            delUpdatedUrl(){
                this.form.UpdatedUrl = "";
                this.UpdatedUrlFile = '';
            },
            delStillsUrl(){
                this.form.StillsUrl = "";
                this.StillsUrlFile = '';
            },

            delUpdatedUrl1(){
                this.form.UpdatedUrl1 = "";
                this.FilmUpdatedUrl1File = '';
            },
            delUpdatedUrl2(){
                this.form.UpdatedUrl2 = "";
                this.FilmUpdatedUrl2File = '';
            },
            delUpdatedUrl3(){
                this.form.UpdatedUrl3 = "";
                this.FilmUpdatedUrl3File = '';
            },

            delActorUrl1(){
                this.form.ActorUrl1 = "";
                this.ActorUrl1File = '';
            },
            delActorUrl2(){
                this.form.ActorUrl2 = "";
                this.ActorUrl2File = '';
            },
            delActorUrl3(){
                this.form.ActorUrl3 = "";
                this.ActorUrl3File = '';
            },
            delActorUrl4(){
                this.form.ActorUrl4 = "";
                this.ActorUrl4File = '';
            },
            delActorUrl5(){
                this.form.ActorUrl5 = "";
                this.ActorUrl5File = '';
            },
            delActorUrl6(){
                this.form.ActorUrl6 = "";
                this.ActorUrl6File = '';
            },

            handleChangeUpdatedUrl(file, fileList) {
                this.form.UpdatedUrl = URL.createObjectURL(file.raw);
                this.UpdatedUrlFile = file.raw;
            },
            handleChangeStillsUrl(file, fileList) {
                this.form.StillsUrl = URL.createObjectURL(file.raw);
                this.StillsUrlFile = file.raw;
            },
            handleChangeUpdatedUrl1(file, fileList) {
                this.form.UpdatedUrl1 = URL.createObjectURL(file.raw);
                this.FilmUpdatedUrl1File = file.raw;
            },
            handleChangeUpdatedUrl2(file, fileList) {
                this.form.UpdatedUrl2 = URL.createObjectURL(file.raw);
                this.FilmUpdatedUrl2File = file.raw;
            },
            handleChangeUpdatedUrl3(file, fileList) {
                this.form.UpdatedUrl3 = URL.createObjectURL(file.raw);
                this.FilmUpdatedUrl3File = file.raw;
            },

            handleChangeActorUrl1(file, fileList) {
                this.form.ActorUrl1 = URL.createObjectURL(file.raw);
                this.ActorUrl1File = file.raw;
            },
            handleChangeActorUrl2(file, fileList) {
                this.form.ActorUrl2 = URL.createObjectURL(file.raw);
                this.ActorUrl2File = file.raw;
            },
            handleChangeActorUrl3(file, fileList) {
                this.form.ActorUrl3 = URL.createObjectURL(file.raw);
                this.ActorUrl3File = file.raw;
            },
            handleChangeActorUrl4(file, fileList) {
                this.form.ActorUrl4 = URL.createObjectURL(file.raw);
                this.ActorUrl4File = file.raw;
            },
            handleChangeActorUrl5(file, fileList) {
                this.form.ActorUrl5 = URL.createObjectURL(file.raw);
                this.ActorUrl5File = file.raw;
            },
            handleChangeActorUrl6(file, fileList) {
                this.form.ActorUrl6 = URL.createObjectURL(file.raw);
                this.ActorUrl6File = file.raw;
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                request({url: 'filmbll/getfilmlist', method:'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                        this.GroupData = res.obj.GroupData;
                    }
                });
            },
            handleClose(done) {
                this.$confirm('确认关闭？')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {});
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 删除操作
            handleDelete(index, row) {
                // 二次确认删除
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        var data = {
                            id:JSON.stringify(row.Id)
                        }
                        request({
                           url: 'filmbll/delfilminfo', method:'post',
                            data:data
                        }).then((res)=>{
                            console.log(res);
                            if (res.code==200){
                                this.$message.success('删除成功');
                                this.tableData.splice(index, 1);
                                this.getData();
                            } else {
                                this.$message.error('删除失败：'+res.msg);
                            }
                        });

                    })
                    .catch(() => {});
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                this.form = row;
                this.editVisible = true;
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={
                    Id:0,
                    InterestWay:1,
                    SettlementWay:1,
                    State:1,
                    BuyLevel:1,
                    ActorUrl6:"",
                    ActorUrl5:"",
                    ActorUrl4:"",
                    ActorUrl3:"",
                    ActorUrl2:"",
                    ActorUrl1:"",

                }
                this.form = aFilm;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                this.form.ActorUrl1File = this.ActorUrl1File;
                this.form.ActorUrl2File = this.ActorUrl2File;
                this.form.ActorUrl3File = this.ActorUrl3File;
                this.form.ActorUrl4File = this.ActorUrl4File;
                this.form.ActorUrl5File = this.ActorUrl5File;
                this.form.ActorUrl6File = this.ActorUrl6File;

                this.form.UpdatedUrlFile = this.UpdatedUrlFile;
                this.form.StillsUrlFile = this.StillsUrlFile;

                this.form.FilmUpdatedUrl1File = this.FilmUpdatedUrl1File;
                this.form.FilmUpdatedUrl2File = this.FilmUpdatedUrl2File;
                this.form.FilmUpdatedUrl3File = this.FilmUpdatedUrl3File;

                if (this.idx===-1) {
                    this.form.id = 0;
                    request({
                       url: 'filmbll/addfilminfo',  method:'file',
                        data:this.form
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`添加成功`);
                            this.getData();
                        } else {
                            this.$message.error('添加失败：'+res.msg);
                        }
                    });
                } else {
                    request({
                        url:'filmbll/updatefilm',  method:'file',
                        data:this.form
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                            this.$set(this.tableData, this.idx, this.form);
                        } else {
                            this.$message.error('更新失败：'+res.msg);
                        }
                    });
                }


            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

